<template>
    <a-layout id="components-layout-demo-responsive" :style="{ height: '100%' }">

      <!-- 左侧菜单栏 start -->
      <a-layout-sider
        breakpoint="lg"
        collapsed-width="0"
        @collapse="onCollapse"
        @breakpoint="onBreakpoint"
      >
        <!-- logo start -->
        <div class="logo" />
        <!-- logo end -->

        <!-- 菜单项 start -->
        <a-menu theme="dark" mode="inline">

          <a-menu-item
            key="/number"
            @click="goToRoute('/number')"
            :class="{ 'ant-menu-item-selected' : isActive('/number') }"
            class="vertically-centered"
          >
            <a-icon type="user" />
            <span style="flex: 1;"> 番号仓库 </span>
          </a-menu-item>

          <a-menu-item
            key="/series"
            @click="goToRoute('/series')"
            :class="{ 'ant-menu-item-selected' : isActive('/series') }"
            class="vertically-centered"
           >
           <a-icon type="video-camera" />
           <span style="flex: 1;"> 系列仓库 </span>
          </a-menu-item>

        </a-menu>
        <!-- 菜单项 end -->

      </a-layout-sider>
      <!-- 左侧菜单栏 end -->

      <!-- 右侧内容区域 start -->
      <a-layout style="width: 100%;">

        <!-- 头部区域 start -->
        <a-layout-header :style="{ background: '#fff', padding: 0, width: '100%' }">
            <a-row type="flex" justify="center" align="middle" style="height: 100%;">
                <a-col :span="0" :md="12" :style="{ padding: '0 25px' }">
                    <a-space :size="8">
                        <a-button @click="handleButtonClick('//github.com/slowfever-Ding')" type="primary" class="vertically-centered" :style="{ padding: '6px 15px' }">
                            <a-icon type="github" />
                        </a-button>
                        <a-button @click="handleButtonClick('//tool.gljlw.com/qqq/?qq=2964481261')" type="primary" class="vertically-centered" :style="{ padding: '6px 15px' }">
                            <a-icon type="qq" />
                        </a-button>
                        <a-button @click="handleButtonClick('//slowfever-ding.github.io/')" type="primary" class="vertically-centered" :style="{ padding: '6px 15px' }">
                            <a-icon type="user" />
                        </a-button>
                    </a-space>
                </a-col>
                <a-col flex="auto">
                    <a-input-search
                        class="vertically-centered"
                        placeholder="搜索神秘代码"
                        enter-button="搜索"
                        size="large"
                        allow-clear
                        @search="onSearch"
                        :style="{ padding: '0 25px' }"
                    />
                </a-col>
            </a-row>
        </a-layout-header>
        <!-- 头部区域 end -->

        <!-- 内容区域 start -->
        <a-layout-content :style="{ height: '100%', margin: '24px 16px 0' }">

          <!-- router-view start -->
          <div :style="{ overflowX: 'hidden', height: '100%', padding: '24px', background: '#fff', minHeight: '100%' }">
            <router-view ref="routerView"></router-view>
          </div>
          <!-- router-view end -->

          <!-- 返回顶部 start -->
          <a-back-top :target="getRouterViewTarget" :visibilityHeight="500">
            <div class="ant-back-top-inner">
              <a-icon type="up" />
            </div>
          </a-back-top>
          <!-- 返回顶部 end -->

        </a-layout-content>
        <!-- 内容区域 end -->

        <!-- 底部区域 start -->
        <a-layout-footer :style="{textAlign: 'center'}">
          Copyright © {{ currentYear }} slowFever All rights reserved.
        </a-layout-footer>
        <!-- 底部区域 end -->

      </a-layout>
      <!-- 右侧内容区域 end -->

    </a-layout>
  </template>

<script>
import { mapMutations } from 'vuex'

export default {
  data () {
    return {
      currentYear: new Date().getFullYear() // 获取当前年份
    }
  },
  methods: {
    ...mapMutations(['setSearchQuery']),
    async goToRoute (route) {
      // 检查当前路由是否与目标路由相同
      if (this.$route.path !== route) {
        try {
          await this.$router.push(route)
        } catch (error) {
          // 处理可能的导航错误
          if (error.name !== 'NavigationDuplicated') {
            console.error(error)
          }
        }
      }
    },
    isActive (route) {
      // 检查当前路由是否与目标路由相同
      return this.$route.path === route
    },
    onCollapse (collapsed, type) {
      console.log(collapsed, type)
    },
    onBreakpoint (broken) {
      console.log(broken)
    },
    onSearch (value) {
      this.setSearchQuery(value) // 更新搜索词到 Vuex
    },
    // 返回 router-view 渲染的 DOM 父元素
    getRouterViewTarget () {
      // console.log(this.$refs.routerView.$el.parentElement)
      return this.$refs.routerView.$el.parentElement
    },
    handleButtonClick (url) {
      // 在新标签页中打开传入的 URL
      window.open(url, '_blank')
    }
  }
}
</script>

<style>
#components-layout-demo-responsive .logo {
  height: 38px;
  background: url('@/assets/logo.png') no-repeat center center;
  background-size: cover; /* 让图片撑满整个容器 */
  margin: 15px;
}
.vertically-centered {
  display: flex;
  align-items: center;
  height: 100%;
}
.ant-card-body {
  overflow: hidden;
  padding: 15px !important;
}
.ant-card-body img {
  transition: transform 0.8s ease;
}
.ant-card-body:hover img {
  transform: scale(1.8);
}
.ant-back-top-inner {
  height: 40px;
  width: 40px;
  line-height: 40px;
  border-radius: 4px;
  background-color: #1890ff;
  color: #fff;
  text-align: center;
  font-size: 20px;
}
</style>
