import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    searchQuery: ''
  },
  mutations: {
    setSearchQuery (state, query) {
      state.searchQuery = query
      console.log('搜索词：', state.searchQuery)
    }
  },
  getters: {
    searchQuery: state => state.searchQuery
  },
  actions: {
  },
  modules: {
  }
})
