<template>
    <div class="series-box">
      <a-list :grid="{ gutter: 16, xs: 1, sm: 2 }" :data-source="searchResults">
        <a-list-item slot="renderItem" slot-scope="item">
          <a-skeleton paragraph active :loading="isShowSkeleton">
            <div class="series-list" @click="copyText(item.content)">{{ item.content }}</div>
          </a-skeleton>
        </a-list-item>
      </a-list>
    </div>
</template>

<script>
import { getSeriesList } from '@/api/series'
import { mapState } from 'vuex'

export default {
  data () {
    return {
      SeriesList: [],
      isShowSkeleton: true
    }
  },
  computed: {
    ...mapState(['searchQuery']),
    searchResults () {
      console.log(this.filterHandleSearchData()) // 搜索结果
      return this.filterHandleSearchData() // 重新计算过滤后的数据
    }
  },
  async mounted () {
    try {
      const res = await getSeriesList()
      console.log(res)
      this.SeriesList = res.series
    } catch (error) {
      console.log('获取失败：', error)
    }

    this.$nextTick(() => {
      // 延迟一秒隐藏骨架屏
      setTimeout(() => {
        this.isShowSkeleton = false
      }, 1000)
    })
  },
  methods: {
    // 点击复制神秘代码
    async copyText (text) {
      try {
        await navigator.clipboard.writeText(text)
        this.$message.success(`${text} 复制成功！`)
      } catch (err) {
        this.$message.error('复制失败！')
        console.error('复制失败:', err)
      }
    },
    // 过滤器处理搜索数据
    filterHandleSearchData () {
      const searchTerm = this.searchQuery.toLowerCase().toString() // 从 Vuex 中获取搜索词
      this.filtered = [] // 创建一个空数组用于存储过滤后的数据

      const filteredValues = this.SeriesList.filter(item => {
        // 将每个字段都转换为小写，并转换为字符串
        const content = (item.content || '').toLowerCase().toString()

        // 检查每个字段是否包含搜索词
        return content.includes(searchTerm)
      })

      // 仅在过滤后的数组不为空时将其添加到 filtered 对象中
      if (filteredValues.length > 0) {
        this.filtered = filteredValues
      }

      return this.filtered
    }
  }
}
</script>

<style lang="scss" scoped>
.series-list {
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
</style>
