<template>
    <div class="number-box">
        <a-row :gutter="[16, 16]">
          <template v-if="searchResults && Object.keys(searchResults).length > 0">
            <a-col v-for="(values, key) in searchResults" :key="key" flex="auto">
              <h1 class="title">{{ key }}</h1>
              <a-skeleton :loading="isShowSkeleton" active :paragraph="{ rows: 2 }">
                <a-list :grid="{ gutter: 16, xs: 1, sm: 2, md: 3, lg: 3, xl: 4, xxl: 6 }" :data-source="values">
                  <a-list-item slot="renderItem" slot-scope="item" :title="item.title" :key="item.fanhao">
                    <a-card :title="item.title" :style="{ textAlign: 'center', borderRadius: '0.375rem', boxShadow: '0 0.125rem 0.25rem rgba(0, 0, 0, 0.075)' }">
                        <img
                          src="//slowfever-ding.github.io/images/loading/loading02.gif"
                          ref="javdbCover"
                          :data-src="item.cover"
                          :alt="item.title"
                          :style="{ maxWidth: '100%', width: '31.25rem', height: '9.375rem', objectFit: 'cover', borderRadius: '0.375rem' }"
                        >
                        <a-row>
                          <a-col :span="24" class="fanhao-box" :style="{ textAlign: 'center', fontSize: '1rem', fontWeight: 'bolder' }" @click.self="copyText(item.fanhao)">
                            {{ item.fanhao }}
                          </a-col>
                        </a-row>
                    </a-card>
                  </a-list-item>
                </a-list>
              </a-skeleton>
            </a-col>
          </template>
          <template v-else>
            <a-empty />
          </template>
        </a-row>

    </div>
</template>

<script>
import { getNumberList } from '@/api/number'
import { mapState } from 'vuex'

export default {
  data () {
    return {
      JavDBDataList: {},
      isShowSkeleton: true
    }
  },
  computed: {
    ...mapState(['searchQuery']),
    searchResults () {
      console.log(this.filterHandleSearchData()) // 搜索结果
      return this.filterHandleSearchData() // 重新计算过滤后的数据
    }
  },
  async mounted () {
    try {
      const res = await getNumberList()
      // console.log(res)
      this.JavDBDataList = res
    } catch (error) {
      console.log('获取失败：', error)
    }

    this.$nextTick(() => {
      // 延迟一秒隐藏骨架屏
      setTimeout(() => {
        this.isShowSkeleton = false
      }, 1000)

      // 图片懒加载
      this.lazyImageLoading()
    })
  },
  updated () {
    this.$nextTick(() => {
      // 图片懒加载
      this.lazyImageLoading()
    })
  },
  methods: {
    // 图片懒加载
    lazyImageLoading () {
      if (!this.observer) {
        this.observer = new IntersectionObserver((entries) => {
          for (const entry of entries) {
            if (entry.isIntersecting) {
              console.count('观察器')
              const img = entry.target
              img.src = img.dataset.src
              this.observer.unobserve(img) // 图片加载后取消观察
            }
          }
        }, {
          root: null, // 根元素
          rootMargin: '0px', // 根元素与视窗之间的距离
          threshold: 0.5 // 触发交叉的阈值
        })
      }

      // 判断 this.$refs.javdbCover 不等于 undefined
      if (this.$refs.javdbCover !== undefined) {
        this.$refs.javdbCover.forEach(item => {
          if (item.dataset.src) { // 只观察还没加载的图片
            this.observer.observe(item)
          }
        })
      }
    },
    // 点击复制神秘代码
    async copyText (text) {
      try {
        await navigator.clipboard.writeText(text)
        this.$message.success(`神秘代码 ${text} 复制成功！`)
      } catch (err) {
        this.$message.error('复制失败！')
        console.error('复制失败:', err)
      }
    },
    // 过滤器处理搜索数据
    filterHandleSearchData () {
      const searchTerm = this.searchQuery.toLowerCase().toString() // 从 Vuex 中获取搜索词
      const filtered = {} // 创建一个空对象用于存储过滤后的数据

      Object.keys(this.JavDBDataList).forEach(key => {
        const filteredValues = this.JavDBDataList[key].filter(item => {
        // 将每个字段都转换为小写，并转换为字符串
          const fanhao = (item.fanhao || '').toLowerCase().toString()
          const title = (item.title || '').toLowerCase().toString()
          const content = (item.content || '').toLowerCase().toString()

          // 检查每个字段是否包含搜索词
          return fanhao.includes(searchTerm) || title.includes(searchTerm) || content.includes(searchTerm)
        })

        // 仅在过滤后的数组不为空时将其添加到 filtered 对象中
        if (filteredValues.length > 0) {
          filtered[key] = filteredValues
        }
      })

      return filtered
    }
  },
  // 组件销毁时断开观察器
  beforeDestroy () {
    if (this.observer) {
      this.observer.disconnect() // 断开观察器
    }
  }
}
</script>

<style lang="scss" scoped>
.fanhao-box {
  padding: 1rem 0;
  backdrop-filter: blur(15px) saturate(180%);
  -webkit-backdrop-filter: blur(15px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: .375rem;
}
.title {
  font-size: 1.25rem;
  font-weight: bolder;
  padding: .3125rem 0;
  // color: rgb(0 0 0 / 0%);
  color: #ff0000;
}
</style>
