import Vue from 'vue'
import VueRouter from 'vue-router'
import NumberView from '@/views/NumberView'
import SeriesView from '@/views/SeriesView'

Vue.use(VueRouter)

const routes = [
  {
    path: '/number',
    component: NumberView
  },
  {
    path: '/series',
    component: SeriesView
  },
  {
    path: '*',
    redirect: '/number'
  }
]

const router = new VueRouter({
  routes
})

export default router
